#footerarea {
    width: 100%;
    height: 500px;
    background: linear-gradient(90deg, rgba(9,9,9,1) 0%, rgba(0,0,0,1) 51%, rgba(129,129,129,1) 100%);
}

#pagelinkarea {
    width: 100%;
    height: 8%;
    background: linear-gradient(90deg, rgba(108,108,108,0.5270483193277311) 0%, rgba(217,217,217,1) 100%);
    text-align: left;
    display: inline-block;
}
#pagelinkbtn {
    width: 100%;
    height: 100%;

    margin-top: 5px;
}
    #pagelinkarea > a {
        text-decoration: none;
    }

    .iconbutton{
        margin: 10px;
        height: 100%;
        text-decoration: none;
    }

#contentarea {
    width: 100%;
    height: 92%;
}
    #gamelogoarea {
        padding: 10px;
        height: 8%;
    }
    #generalconditionsarea {
        padding: 10px;
        height: 8%;
        margin-top: 40px;
        color: gray;
    }
    .linkbutton{
        width: 100px;
        height: 80px;
        padding: 10px;
    }

    .link{
        width: 100px;
        height: 80px;
        padding: 10px;
        color: gray;
        text-decoration: none;
    }

    #playgradearea {
        height: 15%;
    }
    #playrightarea {
        height: 13%;
        margin-top: 30px;
        color: gray;
        font-size: small;
    }
    #companylogoarea {
        height: 16%;
    }
    #companyrightarea {
        height: 14%;
        color: white;
        font-size: small;
    }

    #blankarea {
        height: 10%;
        display: none;
    }

    @media ( max-width: 1190px ) {
        #playrightarea {
            display: none;
        }
        #blankarea {
            display: block;
        }
    }